const renderMarkers = (shops) => {
  const userLocation = { lat: 47.444, lng: -122.176 };
  console.log(shops);
  let filteredArray = [];

  shops.forEach((shop) => {
    let result = geolib.isPointWithinRadius(
      { latitude: shop.latitude, longitude: shop.longitude },
      { latitude: userLocation.lat, longitude: userLocation.lng },
      5000
    );

    if (result) {
      filteredArray.push(shop);
    }
  });

  return filteredArray.map((shop, index) => {
    return (
      <Marker
        key={index}
        onClick={onMarkerClick(shop)}
        title={shop.name}
        position={{ lat: shop.latitude, lng: shop.longitude }}
        name={shop.name}
        // {...console.log(shop.latitude)}
      />
    );
  });
};

const MapView = (props) => {
  const mapStyles = {
    width: "100%",
    height: "100%",
  };

  return (
    <Map
      google={props.google}
      zoom={12}
      style={mapStyles}
      initialCenter={{ lat: 47.444, lng: -122.176 }}
      radius={25}
    >
      {renderMarkers(props.shops)}
    </Map>
  );
};

MapView.propTypes = {
  shops: PropTypes.array,
};

const mapStateToProps = (state) => {
  console.log(state);
  return {
    shops: state.shopData.shops,
  };
};

